<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <div class="support" v-if="seller.supports">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div class="support-count" v-if="seller.supports" @click="detailShowFunc">
        <span class="count">{{seller.supports.length}}个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <div class="bulletin-wrapper" @click="detailShowFunc">
      <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar" width="100%" height="100%">
    </div>
    <transition name="fade">
      <div class="detail" v-show="detailShow">
        <div class="detail-wrapper clearFix">
          <div class="detail-main">
            <h1 class="name">{{seller.name}}</h1>
            <div class="star-wrapper">
              <star :size="48" :score="seller.score"></star>
            </div>
            <titleLine text="商家信息"></titleLine>
            <ul class="supports" v-if="seller.supports">
              <li v-for="(item,index) in seller.supports" :key="item.index" class="support-item">
                <span :class="classMap[seller.supports[index].type]" class="icon"></span>
                <span class="text">{{seller.supports[index].description}}</span>
              </li>
            </ul>
            <title-line text="商家公告"></title-line>
            <div class="bulletin">
              <p class="content">{{seller.bulletin}}</p>
            </div>
          </div>
        </div>
        <div class="detail-close">
          <i class="icon-close" @click="detailCloseFunc"></i>
        </div>
    </div>
    </transition>
  </div>
</template>
<script type="text/ecmascript-6">
  import star from '.././star/star';
  import titleLine from '.././title/title';
  export default{
    props: {
      seller: {
        type: Object
      }
    },
    data() {
      return {
        detailShow: false
      };
    },
    methods: {
      detailShowFunc() {
        this.detailShow = true;
      },
      detailCloseFunc() {
        this.detailShow = false;
      }
    },
    created() {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    },
    components: {
      star,
      titleLine
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import '../../common/style/mixin.styl'

  @keyframes fade-in
    0%
      opacity: 0px
      background: rgba(7,17,27,0)
    100%
      opacity: 1px
      background: rgba(7,17,27,0.8)
  @keyframes fade-out
    0%
      opacity: 1px
      background: rgba(7,17,27,0.8)
    100%
      opacity: 0px
      background: rgba(7,17,27,0)
  .header
    overflow: hidden
    color: #fff
    background: rgba(7, 17, 25, 0.5)
    position: relative
    .content-wrapper
      padding: 24px 12px 18px 24px
      font-size: 0px
      position: relative
      .avatar
        display: inline-block
        vertical-align: top
        img
          -webkit-border-radius: 2px
          -moz-border-radius: 2px
          border-radius: 2px
      .content
        display: inline-block
        margin-left: 16px
        font-size: 14px
        .title
          margin: 2px 0 8px 0px
          .brand
            display: inline-block
            vertical-align top
            width: 30px
            height: 18px
            bg-image('brand')
            background-size: 30px 18px
            background-repeat: no-repeat
          .name
            margin-left: 6px
            font-size: 16px
            font-weight: bold
            line-height: 18px
        .description
          margin-bottom: 10px
          line-height: 12px
          font-size: 12px
        .support
          .icon
            display: inline-block
            vertical-align: bottom
            width: 12px
            height: 12px
            margin-right: 4px
            background-size: 12px 12px
            background-repeat: no-repeat
            &.decrease
              bg-image('decrease_1')
            &.discount
              bg-image('discount_1')
            &.guarantee
              bg-image('guarantee_1')
            &.invoice
              bg-image('invoice_1')
            &.special
              bg-image('special_1')
          .text
             line-height: 12px
             font-size: 10px
      .support-count
        position: absolute
        right: 12px
        bottom: 14px
        padding: 0 8px
        height: 24px
        line-height: 24px
        border-radius: 14px
        background: rgba(0, 0, 0, 0.2)
        .count
          font-size: 10px
        .icon-keyboard_arrow_right
          margin-left: 2px
          line-height: 24px
          font-size: 10px
    .bulletin-wrapper
      position: relative
      background: rgba(7, 17, 27, 0.2)
      height: 28px
      line-height: 28px
      padding: 0 22px 0 12px
      white-space nowrap
      overflow: hidden
      -ms-text-overflow: ellipsis
      text-overflow: ellipsis
      .bulletin-title
        display: inline-block
        vertical-align: top
        margin-top: 8px
        width: 22px
        height: 12px
        bg-image('bulletin')
        background-size: 22px 12px
        background-repeat: no-repeat
      .bulletin-text
        vertical-align: top
        font-size: 10px
        margin: 0 4px
      .icon-keyboard_arrow_right
        position: absolute
        font-size: 10px
        right: 12px
        top: 9px

    .background
       position: absolute
       width: 100%
       height: 100%
       left: 0px
       top: 0px
       z-index: -1
       filter: blur(10px)
    .fade-enter-active
      -webkit-animation: fade-in 0.2s
      -o-animation: fade-in 0.2s
      animation: fade-in 0.2s
    .fade-leave-active
      -webkit-animation: fade-out 0.2s
      -o-animation: fade-out 0.2s
      animation: fade-out 0.2s
    .detail
      position: fixed
      top: 0px
      left: 0px
      z-index: 100
      width: 100%
      height: 100%
      overflow: auto
      transition: all 0.2s
      background: rgba(7,17,27,0.8)
      -webkit-backdrop-filter: blur(10px)
      .detail-wrapper
        min-height: 100%
        width: 100%
        .detail-main
          margin-top: 64px
          padding-bottom: 64px
          .name
            font-size: 16px
            text-align center
            line-height: 16px
            font-weight: 700
          .star-wrapper
            margin-top: 18px
            padding: 2px 0px
            text-align: center
          .supports
            width: 80%
            margin: 0 auto
            .support-item
              padding: 0 12px
              margin-bottom: 12px
              font-size: 0px
              &:last-child
                margin-bottom: 0
              .icon
                display: inline-block
                width: 16px
                height: 16px
                vertical-align: top
                margin-right: 6px
                background-size: 16px 16px
                background-repeat: no-repeat
                &.decrease
                  bg-image('decrease_2')
                &.discount
                  bg-image('discount_2')
                &.guarantee
                  bg-image('guarantee_2')
                &.invoice
                  bg-image('invoice_2')
                &.special
                  bg-image('special_2')
              .text
                font-size: 12px
                line-height: 16px
          .bulletin
            width: 80%
            margin: 0 auto
            .content
              font-size: 12px
              line-height: 24px
              font-weight: 200
              padding: 0 12px
              color: rgb(255, 255, 255)
      .detail-close
        position: relative
        width: 32px
        height: 32px
        margin: -64px auto 0 auto
        clear: both
        font-size: 32px
</style>



